import styled from "styled-components";

import { RBK } from "../../assets/images/Brands/RBK";
import { Interfacs } from "../../assets/images/Brands/Interfacs";
import { Moscow24 } from "../../assets/images/Brands/Moscow24";

import { Text } from "../../assets/styles/component";

const SBrandsList = styled.div`
  margin: 0 8.3%;
  margin-bottom: 180px;
  display: flex;
  align-items: center;
  gap: 50px;
  will-change: scroll-position;

`;

const LogosContainer = styled.div`
  display: grid;
  width: 100%;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(4, 1fr);

  gap: 20px;

  & p {
    color: #000;
    max-width: 238px;
    font-size: 20px;
    margin-bottom: 10px;
  }
`;

const LogoBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 160px;

  
  background: rgba(205, 205, 205, 0.15);
  border: 2px solid rgba(241, 249, 255, 0.25);
  border-radius: 30px;
  
  & svg,
  & img {
    max-width: 70%;
  }

  @media (max-width: 1290px) {
    height: 140px;
  }
`;

const Public = styled.div`
  grid-column-start: 1;
  grid-row-start: 2;
  grid-row-end: 4;
  align-self: flex-end;
`;

const Email = styled.a`
  font-weight: 400;
  font-size: 22px;
  line-height: 150%;

  color: #126bd1;
  text-decoration: underline;
`;

const BrandsList = () => {
  const Logos = [
    <Moscow24 />,
    <RBK />,
    <Interfacs />,
    <RBK />,
    <Interfacs />,
    <Moscow24 />,
    <Moscow24 />,
    <RBK />,
    <Interfacs />,
  ];

  return (
    <SBrandsList>
      <LogosContainer>
        {Logos.map((logo) => (
          <LogoBox key={Math.random()}>{logo}</LogoBox>
        ))}
        <Public>
          <Text>Хотите опубликовать о нас материал?</Text>
          <Email href="mailto:ads@socialjet.ru" aria-label={"Написать нам на почту"}>ads@socialjet.ru</Email>
        </Public>
      </LogosContainer>
    </SBrandsList>
  );
};

export default BrandsList;
