import styled from "styled-components";
import { css } from "styled-components";
import { useState } from "react";

import { A, Text, Title3 } from "../../assets/styles/component";
import CheckBox from "./CheckBox";
import Button from "../../assets/UI/ButtonDesktop";

import { Telegram } from "../../assets/images/Socials/Telegram";

import { EngLoc } from "../../assets/constants/localizations";

const SForm = styled.form`
  display: flex;
  padding: 150px 8.3%;
  position: relative;
  overflow: hidden;
  background: #000f67;

  & button {
    background: #ffffff;
    border-radius: 125px;
    width: fit-content;
    padding: 30px 65px;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &::before {
    z-index: 0;
    background: #000f67;
  }

  &::after {
    z-index: 1;
    overflow: hidden;
    background: radial-gradient(
        43.18% 87.18% at 67.39% 25.75%,
        #ff7cda 0%,
        rgba(255, 124, 218, 0) 100%
      ),
      radial-gradient(
        25.12% 79.02% at 32.93% 125.8%,
        #00aee8 0%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        38.5% 124.42% at 61.09% 172.86%,
        #00aee8 0%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        60.8% 152.7% at -13.59% 11.65%,
        #00aee8 0%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        64.16% 160.68% at 112.44% 38.89%,
        #7400e8 0%,
        rgba(116, 0, 232, 0) 100%
      ),
      radial-gradient(
        26.08% 44.57% at 5.86% 120.25%,
        #7400e8 0%,
        rgba(116, 0, 232, 0) 100%
      );
  }
`;

const P = styled(Text)``;

const FormBox = styled.div`
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: left;
`;

const Div = css`
  display: flex;
  flex-direction: column;
`;

const TitleBox = styled.div`
  ${Div}
  gap: 70px;
  margin-bottom: 40px;
`;

const Title = styled(Title3)`
  margin-bottom: 24px;
  font-weight: 400;
  font-size: 96px;
  line-height: 110%;
  max-width: 967px;
`;

const Fields = css`
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;

  height: 80px;

  margin-bottom: 15px;
  padding-left: 34px;
  outline: none;
  box-sizing: border-box;

  &::placeholder,
  & {
    font-family: "HeliosExtC", -apple-system, BlinkMacSystemFont, "Segoe UI",
      "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #fff;
  }

  &:focus {
    outline: 2px solid rgba(255, 255, 255, 0.3);
  }
`;

const InputsBox = styled.div`
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-bottom: 40px;
  gap: 14px;

  & svg {
    position: absolute;
    opacity: 0.3;
    top: 27px;
    left: 250px;
  }
`;

const Input = styled.input`
  ${Fields}
  min-width: 329px;
  max-width: 400px;
`;

const TextAreaBox = styled.div`
  position: relative;
  width: 90%;

  & p {
    position: absolute;
    top: 26px;
    left: 205px;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #ffffff66;
  }
`;

const Textarea = styled.textarea`
  ${Fields}
  width: 100%;
  max-width: 580px;
  resize: none;
  padding-top: 25px;

  position: relative;
`;

const ToggleBox = styled.div`
  display: flex;
  gap: 60px;
  margin-bottom: 45px;
`;

const Link = styled(A)`
  color: #fff;
  text-decoration: underline;
  font-weight: 400;
  font-size: 12px;
  line-height: 160%;

  &:hover {
    color: #abe4f7;
  }
`;

const ButtonBox = styled.div`
  display: flex;
  align-items: center;
  gap: 32px;
  text-align: left;

  p {
    max-width: 337px;
    color: #ffffff99;
  }
`;

const RequestForm = ({ language }) => {
  const [value, setValue] = useState("");
  const [valueTextArea, setValueTextArea] = useState("");

  return (
    <SForm id={"form"}>
      <FormBox>
        <TitleBox>
          <Title>
            {language === "Ru"
              ? "Хотите запустить рекламу?"
              : EngLoc.social_media.form.title}
          </Title>
          <P size="26">
            {language === "Ru"
              ? "Отправьте заявку и мы сделаем всё красиво"
              : EngLoc.social_media.form.p}
          </P>
        </TitleBox>

        <InputsBox>
          <Telegram />
          <Input
            placeholder={
              language === "Ru"
                ? "Ваш номер или ник"
                : EngLoc.social_media.form.input
            }
            onChange={(e) => setValue(e.target.value)}
          />

          <TextAreaBox>
            {valueTextArea.length === 0 && (
              <Text>
                {language === "Ru"
                  ? "(не обязательно)"
                  : EngLoc.social_media.form.textarea_after}
              </Text>
            )}

            <Textarea
              placeholder={
                language === "Ru"
                  ? "Опишите задачу"
                  : EngLoc.social_media.form.textarea
              }
              onChange={(e) => {
                setValueTextArea(e.target.value);
              }}
            ></Textarea>
          </TextAreaBox>
        </InputsBox>

        <ToggleBox>
          <CheckBox
            name="call"
            text={
              language === "Ru"
                ? "Позвоните прямо сейчас"
                : EngLoc.social_media.form.checkboxes[0]
            }
          />
          <CheckBox
            name="send_message"
            text={
              language === "Ru"
                ? "Напишите мне в Телеграм"
                : EngLoc.social_media.form.checkboxes[1]
            }
          />
        </ToggleBox>

        <ButtonBox>
          <Button type="secondary" colorTxt="#2850BF">
            {language === "Ru" ? "Запустить рекламу" : EngLoc.buttons.send}
          </Button>
          {language === "Ru" ? (
            <Text size="12">
              Нажимая на кнопку, вы даете согласие на обработку{" "}
              <Link
                targer="_blank"
                href="/privacy"
                aria-label={"Прочитать политику обработки персональных данных"}
              >
                персональных данных
              </Link>{" "}
              и соглашаетесь c{" "}
              <Link
                targer="_blank"
                href="/terms"
                aria-label={"Прочитать политику конфиденциальности"}
              >
                политикой конфиденциальности
              </Link>
            </Text>
          ) : (
            <Text size="12">
              By clicking on the button, you consent to the processing of
              <Link
                targer="_blank"
                href="/privacy"
                aria-label={"Прочитать политику обработки персональных данных"}
              >
                personal data
              </Link>{" "}
              and agree to
              <Link
                targer="_blank"
                href="/terms"
                aria-label={"Прочитать политику конфиденциальности"}
              >
                {" "}
                the privacy policy
              </Link>
            </Text>
          )}
        </ButtonBox>
      </FormBox>
    </SForm>
  );
};

export default RequestForm;
