import styled from "styled-components";
import { Text } from "../../assets/styles/component";

const SCheckBox = styled.label`
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;
  height: 40px;
  max-width: 300px;

  padding-left: 65px;
  margin-bottom: 12px;

  font-size: 22px;
  user-select: none;
  cursor: pointer;

  & input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  & span {
    position: absolute;
    top: 0;
    left: 0;
    height: 40px;
    width: 40px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 10px;
  }

  span:after {
    content: "";
    position: absolute;
    opacity: 0;
    transition: 0.1s;
  }

  & input:checked ~ span:after {
    opacity: 1;
  }

  & span:after {
    left: 10px;
    top: 10px;
    width: 20px;
    height: 20px;

    background: linear-gradient(290.71deg, #3cc5ff -23.07%, #ffa4f6 147.25%);
    border-radius: 5px;
  }
`;

const SText = styled(Text)`
  font-size: 16px;
`;

const CheckBox = ({ name, text }) => {
  return (
    <SCheckBox>
      <SText>{text}</SText>
      <input name="name" type="checkbox" />
      <span></span>
    </SCheckBox>
  );
};

export default CheckBox;
